@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@1,600&family=Roboto+Slab:wght@600&display=swap");

@font-face {
  font-weight: 700;
  font-family: "HeadingNow Trial";
  font-style: italic;
  src: url(../../fonts/fonnts.com-HeadingNowTrial-46BoldItalic.woff2) format("woff2"), url(../../fonts/fonnts.com-HeadingNowTrial-46BoldItalic.woff) format("woff");
  font-display: swap;
}

body {
  min-width: 320px;
  margin: 0;
  font-family: "Roboto Condensed", sans-serif;
}

img {
  max-width: 100%;
  object-fit: contain;
}

.container {
  position: relative;
  z-index: 1;
  box-sizing: border-box;
  max-width: 340px;
  margin-right: auto;
  margin-left: auto;
  padding: 0 10px;
}

.bottom {
  position: relative;
  padding-top: 57px;
  background-image: url(../../img/bg.png);
  background-repeat: no-repeat;
  background-position: center -230px;
}

.bottom::before {
  content: "";
  position: absolute;
  bottom: 6%;
  left: calc(50% - 63px);
  width: 274px;
  height: 410px;
  background-image: url(../../img/people-bottom.png);
  background-repeat: no-repeat;
  background-position: 50% bottom;
  background-size: contain;
  transform: translateX(-50%);
}

.title {
  display: grid;
  margin: 0 0 6px;
  font-weight: 700;
  font-size: 53px;
  line-height: 100%;
  font-family: "HeadingNow Trial", sans-serif;
  color: #196501;
  text-transform: uppercase;
  font-style: italic;
  -webkit-text-stroke: 0.5px #ffffff;
}

.title span {
  margin: 0 -4px;
  padding: 0 4px;
  background: linear-gradient(to top, #196501, #33cb03);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-box-decoration-break: clone;
  box-decoration-break: clone;
  -webkit-text-fill-color: transparent;
  text-fill-color: transparent;
}

.title span:nth-child(even) {
  justify-self: end;
}

.subtitle {
  width: 59%;
  margin: 0 0 20px auto;
  font-weight: 600;
  font-size: 35px;
  line-height: 115%;
  color: #FB841D;
  font-style: italic;
  background: linear-gradient(to top, #FB841D, #F2A014);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-box-decoration-break: clone;
  box-decoration-break: clone;
  -webkit-text-fill-color: transparent;
  text-fill-color: transparent;
}

.badges {
  display: flex;
  width: fit-content;
  margin: 0 -14px 0 auto;
  padding: 0;
  list-style: none;
}

.badges img {
  width: 53px;
  height: 53px;
}

.hca {
  position: relative;
  z-index: 2;
  display: block;
  width: 73px;
  height: 73px;
  margin-left: 54%;
}

.prod-tube {
  position: relative;
  display: grid;
  grid-template-columns: 41px 38px 17px 38px 37px;
  grid-template-rows: 16px auto 16px;
  width: fit-content;
  margin: -30px 0 0 20%;
}

.prod-tube::before {
  content: "";
  position: absolute;
  top: -16px;
  left: 58%;
  z-index: -1;
  width: 250px;
  height: 204px;
  background-image: url(../../img/decor.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transform: translateX(-50%);
}

.prod-tube img:first-child {
  grid-column-end: 3;
  grid-column-start: 1;
  grid-row-end: -1;
  grid-row-start: 2;
}

.prod-tube img:nth-child(2) {
  position: relative;
  z-index: 1;
  grid-column-end: 5;
  grid-column-start: 2;
  grid-row-end: 2;
}

.prod-tube img:nth-child(3) {
  grid-column-end: -1;
  grid-column-start: 4;
  grid-row-end: -1;
  grid-row-start: 2;
}

.button {
  display: inline-flex;
  align-items: center;
  box-sizing: border-box;
  padding: 8px 18px;
  font-weight: 700;
  font-size: 52px;
  line-height: 100%;
  font-family: "HeadingNow Trial", sans-serif;
  text-align: center;
  color: #5F0000;
  text-transform: uppercase;
  text-decoration: none;
  font-style: italic;
  text-shadow: -1.3px 1.3px 0.6px #ffffff;
  background-color: #FB841D;
  background-image: linear-gradient(to top, #F46F01, #FFAC00 36%, #FE5800 80%);
  border-width: 4px 5px;
  border-style: solid;
  border-color: #c5c5c5;
  border-radius: 17px;
  appearance: none;
  gap: 6px;
}

.button::after {
  content: "";
  display: block;
  width: 28px;
  height: 37px;
  background-image: url(../../img/button-arrow.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.bottom .button {
  position: relative;
  z-index: 1;
  width: 100%;
  max-width: 320px;
  margin-top: -20px;
}

.bottom .button::before {
  content: "";
  position: absolute;
  right: -16px;
  bottom: calc(100% + 15px);
  width: 84px;
  height: 122px;
  background-image: url(../../img/arrow-color.png);
  background-repeat: no-repeat;
}

@media (min-width: 990px) {
  .container {
    max-width: 1260px;
  }

  .title {
    max-width: 818px;
    margin-bottom: 33px;
    margin-left: auto;
    font-size: 134px;
  }

  .title span {
    margin-right: -8px;
    padding-right: 8px;
    padding-left: 8px;
  }

  .subtitle {
    width: fit-content;
    max-width: 330px;
    margin-right: 26px;
    font-size: 34px;
    text-align: right;
  }

  .hca {
    width: 125px;
    height: 125px;
    margin-top: -186px;
    margin-left: 48%;
  }

  .badges {
    margin-right: 100px;
  }

  .badges img {
    width: 96px;
    height: 96px;
  }

  .prod-tube {
    grid-template-columns: 85px 72px 45px 72px 78px;
    grid-template-rows: 35px auto 35px;
    margin: -126px 0 0 10%;
  }

  .prod-tube::before {
    top: 4%;
    left: 93%;
    width: 430px;
    height: 352px;
  }

  .button {
    padding: 14px 28px;
    font-size: 82px;
    border-width: 7px 8px;
    border-radius: 28px;
  }

  .button::after {
    width: 46px;
    height: 59px;
  }

  .bottom .button {
    display: flex;
    width: fit-content;
    max-width: none;
    margin-top: -104px;
    margin-left: auto;
  }

  .bottom::before {
    bottom: -10%;
    left: 0;
    width: 555px;
    height: 849px;
    transform: translateX(0);
  }
}

@media (min-width: 1440px) {
  .bottom::before {
    bottom: -8%;
    left: calc(50% - 440px);
    transform: translateX(-50%);
  }

  .subtitle {
    max-width: 440px;
  }

  .hca {
    margin-left: 53%;
  }

  .prod-tube {
    margin-left: 19%;
  }

  .bottom .button::before {
    right: -81px;
    bottom: calc(100% - 14px);
  }
}

@media (max-width: 370px) {
  .title span:nth-child(even) {
    margin-right: -10px;
  }

  .subtitle {
    margin-right: -10px;
  }

  .bottom .button {
    width: auto;
    margin-right: -10px;
    margin-left: -10px;
  }
}